import { FC, ReactElement } from "react";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";
import { App, Auth, FC_SideNav, FT_SideNav } from "../../actions";
import { NavLink } from "react-router-dom";
import {
  MdOutlineAccountCircle,
  MdOutlineFolderOpen,
  MdOutlineHome,
  MdOutlineSettings,
  MdPostAdd,
} from "react-icons/md";
import { UserRoles } from "../../utils/access";

type RenderBuiltInComponentProps = {
  auth: Auth;
  app: App;
  FC_SideNav: FT_SideNav;
};

const _RenderBuiltInComponent: FC<RenderBuiltInComponentProps> = ({
  app,
  auth,
  FC_SideNav,
}): ReactElement | null => {
  // let access_list = auth.selected_access?.access;

  if (auth.isAuthenticated === false) return null;
  return (
    <nav
      className={`overflow-auto
          dark:bg-dark-800 dark:text-white 
        pr-2 pl-0 py-1 border-gray-200 dark:border-gray-700 
         p-2 pt-20 fixed bottom-0 ${
           app.side_menu === true
             ? "lg:left-0 left-0 bg-white border-r"
             : "-left-80 lg:left-0"
         }  top-0 z-40 w-52
    `}
    >
      <div className="mb-1 side-navigation-container">
        <NavLink
          to={"/"}
          onClick={FC_SideNav.bind(this, false)}
          className={`navigation-item `}
          exact
        >
          <div className="mr-1">
            <MdOutlineHome className="text-2xl" />
          </div>
          <span className="flex-1">Page d'accueil</span>
        </NavLink>

        <NavLink
          to={"/dashboard"}
          onClick={FC_SideNav.bind(this, false)}
          className={`navigation-item `}
          exact
        >
          <div className="mr-1">
            <MdOutlineFolderOpen className="text-2xl" />
          </div>
          <span className="flex-1">Documents</span>
        </NavLink>

        {auth.selected_access?.constant === UserRoles.SECRETARY && (
          <NavLink
            to={"/create-document"}
            onClick={FC_SideNav.bind(this, false)}
            className={`navigation-item `}
            exact
          >
            <div className="mr-1">
              <MdPostAdd className="text-2xl" />
            </div>
            <span className="flex-1">Nouveau document</span>
          </NavLink>
        )}

        <NavLink
          to={"/users"}
          onClick={FC_SideNav.bind(this, false)}
          className={`navigation-item `}
          exact
        >
          <div className="mr-1">
            <MdOutlineAccountCircle className="text-2xl" />
          </div>
          <span className="flex-1">Compte d'utilisateur</span>
        </NavLink>

        <NavLink
          to={"/settings"}
          onClick={FC_SideNav.bind(this, false)}
          className={`navigation-item `}
          exact
        >
          <div className="mr-1">
            <MdOutlineSettings className="text-2xl" />
          </div>
          <span className="flex-1">Paramètre</span>
        </NavLink>
      </div>
    </nav>
  );
};

const mapStateToProps = ({
  app,
  auth,
}: StoreState): { app: App; auth: Auth } => {
  return { app: app, auth: auth };
};

const RenderBuiltInComponent = connect(mapStateToProps, { FC_SideNav })(
  _RenderBuiltInComponent
);

export default RenderBuiltInComponent;
